  transform: rotate3d(0, 0, 1, -45deg);
}

.tabs {
  margin-top: 1.375em;
  margin-bottom: 1.375em;
}
.tabs__links {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  cursor: pointer;
  border-top: 1px solid hsl(0, 0%, 60%);
  border-bottom: 1px solid hsl(0, 0%, 60%);
}
.no-js .tabs__links {
  display: none;
}
.tabs__links:after {
  content: "";
  flex-grow: 1;
  height: 1px;
}
.tabs__link-wrap {
  position: relative;
}
.tabs__link-wrap:not(:last-child) {
  border-right: none;
}
.tabs__link-wrap--active .tabs__link {
  color: var(--secondary-color);
  background-color: #fff;
}
.tabs__link-wrap--active .tabs__link {
  position: relative;
  color: var(--secondary-color) !important;
}
.tabs__link-wrap--active .tabs__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--secondary-color);
  bottom: -2px;
  left: 0;
}
.tabs__link {
  display: block;
  padding: 10px;
  color: var(--light-text-color);
  font-family: -apple-system, BlinkMacSystemFont, "Manrope", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px; /* 133.333% */
  text-decoration: none;
}
.tabs__link:hover, .tabs__link:focus {
  color: hsl(0, 0%, 50%);
  background: #E9FBE5;
}
.tabs__content-wrapper {
  display: block;
  padding: 0.3em 0.7em;
  border-top: none;
}
.tabs__content-item {
  display: none;
  margin-bottom: 30px;
}
.js .tabs__content-item {
  display: none;
}
.tabs__content-item--active {
  display: block;
}
.js .tabs__content-item--active {
  display: block;
}
.tabs__content-wrap {
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid var(--light-text-color);
  border-radius: 5px;
  background-color: var(--background-tab-color); /* Замените на нужный вам цвет */
}
.tabs__content-title {
  margin: 0;
  margin-bottom: 20px;
  color: var(--secondary-color);
  font-family: -apple-system, BlinkMacSystemFont, "Tahoma", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.tabs__content-description {
  color: hsl(0, 0%, 0%);
  font-family: -apple-system, BlinkMacSystemFont, "Manrope", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.tabs__content-heading {
  color: hsl(0, 0%, 0%);
  font-family: -apple-system, BlinkMacSystemFont, "Manrope", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.tabs__content-descr {
  color: hsl(0, 0%, 0%);
  font-family: -apple-system, BlinkMacSystemFont, "Manrope", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.tabs__features-list {
  list-style: none;
  padding-left: 0; /* Отступ слева для маркера */
}
.tabs__features-item {
  position: relative;
  padding: 15px 16px;
  padding-left: 30px;
  background: white;
  margin-bottom: 3px;
  color: hsl(0, 0%, 0%);
  font-family: -apple-system, BlinkMacSystemFont, "Manrope", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.tabs__features-item::before {
  position: absolute;
  top: 20px;
  left: 10px;
  content: "";
  display: inline-block;
  width: 8px; /* Размер маркера */
  height: 8px;
  background-color: var(--secondary-color); /* Цвет маркера */
  border-radius: 50%; /* Сделать маркер круглым */
  margin-right: 10px;
}
.tabs__composition-list {
  display: flex;
  gap: 14px;
  list-style: none;
  padding-left: 0;
}
.tabs__composition-item {
  padding: 20px;
  background: white;
  margin-bottom: 3px;
  color: hsl(0, 0%, 0%);
  font-family: -apple-system, BlinkMacSystemFont, "Manrope", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a,
  a:visited {
    text-decoration: underline !important;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  a[href^="#"]:after,
  a[href^="javascript:"]:after {
    content: "";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  pre,
  blockquote {
    border: 1px solid #999 !important;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr,
  img {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
  table {
    border-collapse: collapse !important;
  }
  table td,
  table th {
    background-color: #fff !important;
  }
}